<template>
  <div>
    <el-upload
      :disabled="disabled"
      :class="{'off-add': this.limit<=defaultFile.length}"
      :ref="refUpload"
      list-type="picture-card"
      action=""
      :on-preview="handlePictureCardPreview"
      :before-upload="handleBeforeUpload"
      :on-remove="handleRemove"
      :file-list="defaultFile"
    >
      <i class="el-icon-plus"></i>
    </el-upload>

    <el-dialog :append-to-body="true" :visible.sync="dialogVisible">
      <img style="width:100%" :src="dialogImageUrl" alt="" />
    </el-dialog>

    <el-dialog :append-to-body="true" :visible.sync="modalImg" title="">
      <div class="cropper">
        <vueCropper
          style="width:100%;height:500px;"
          ref="cropper"
          :img="option.img"
          :outputSize="option.size"
          :outputType="option.outputType"
          :info="true"
          :full="option.full"
          :canMove="option.canMove"
          :canMoveBox="option.canMoveBox"
          :original="option.original"
          :autoCrop="option.autoCrop"
          :fixed="option.fixed"
          :fixedNumber="option.fixedNumber"
          :centerBox="option.centerBox"
          :infoTrue="option.infoTrue"
          :fixedBox="option.fixedBox"
          :canScale="option.canScale"
          :autoCropWidth="autoCropWidth"
          :autoCropHeight="autoCropHeight"
        ></vueCropper>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modalImg = false"> </el-button>
        <el-button :loading="loading" type="primary" @click="uploadSuccess()"> </el-button>
      </span>
      <!--cropper-->
    </el-dialog>
  </div>
</template>
<script>
import { VueCropper } from 'vue-cropper'
// import { uploadAPI } from '@/api'
export default {
  name: 'YUpload',
  components: {
    VueCropper
  },
  props: {
    disabled: {
      type: Boolean, // disabled === true 
      default: false
    },
    limit: {
      type: Number,
      default: 4
    },
    refUpload: {
      type: String,
      default: 'upload'
    },
    fileList: {
      type: Array,
      default: () => []
    },
    /*  */
    autoCropWidth: {
      type: [String, Number],
      default: 360
    },
    autoCropHeight: {
      type: [String, Number],
      default: 360
    },
    fixedBox: {
      type: Boolean,
      default: () => true
    }, //  
    fixed: {
      type: Boolean,
      default: () => true
    }, // 
    fixedNumber: {
      type: Array,
      default: () => [1, 1]
    }, // 
    /*  */
    isCropper: {
      type: Boolean,
      default: () => true
    }
  },
  data() {
    return {
      loading: false,
      dialogImageUrl: '',
      defaultFile: [],
      img: '',
      dialogVisible: false,
      modalImg: false
    }
  },
  computed: {
    option() {
      return {
        img: this.img, // 
        info: true, // 
        outputSize: 0.8, // 
        outputType: 'png', // 
        canScale: true, // 
        autoCrop: true, // 
        autoCropWidth: this.autoCropWidth, // 
        autoCropHeight: this.autoCropHeight, // 
        fixedBox: this.fixedBox, //  
        fixed: this.fixed, // 
        fixedNumber: this.fixedNumber, // 
        full: true, // 
        canMoveBox: false, // 
        original: false, // 
        centerBox: false, // 
        infoTrue: false // true  false 
      }
    }
  },
  methods: {
    handleRemove(file, fileList) {
      this.default = fileList
      this.defaultFile = fileList
      this.$emit('on-remove', {
        file, fileList
      })
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    async uploadSuccess() {
      if (this.isCropper) {
        this.$refs.cropper.getCropBlob(async data => {
          try {
            this.loading = true
            const formData = new FormData()
            formData.append('file', this.blobtoFile(data))
            // const res = await uploadAPI.upload(formData)
            // this.$refs[this.refUpload].fileList.push({
            //   url: res.data.url,
            //   name: '',
            //   status: 'finished'
            // })
            this.defaultFile = this.$refs[this.refUpload].fileList
            // this.handleSuccess(res)
            this.modalImg = false
          } catch (e) {
            console.error(e)
          } finally {
            this.loading = false
          }
        })
      } else {
        const formData = new FormData()
        formData.append('file', this.fileinfo)
        // const res = await uploadAPI.upload(formData)
        // this.$refs[this.refUpload].fileList.push({
        //   url: res.data.url,
        //   name: '',
        //   status: 'finished'
        // })
        // this.defaultFile = this.$refs[this.refUpload].fileList
        // this.handleSuccess(res)
        // this.modalImg = false
      }
    },
    blobtoFile(blob) {
      // blobBase64
      const name = this.fileinfo.name.split('.')[0] + '.png'
      return new window.File([blob], name, { type: 'image/png' })
    },
    handleBeforeUpload(file) {
      const check = this.fileList.length >= this.limit
      if (check) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return false
      }
      const testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
      const imgExt = /(jpg|JPG|bmp|BMP|gif|GIF|ico|ICO|pcx|PCX|jpeg|JPEG|tif|TIF|png|PNG|raw|RAW|tga|TGA)$/
      if (!imgExt.test(testmsg) && this.isCropper) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return false
      }
      this.fileinfo = file
      const data = window.URL.createObjectURL(new Blob([file]))
      this.img = data
      if (this.isCropper) {
        this.modalImg = true
      } else {
        this.uploadSuccess()
      }
      return false // 
    },
    handleSuccess(response) {
      this.$emit('on-success', { file: response.data, fileList: this.defaultFile })
    }
  },
  watch: {
    fileList() {
      this.formatDefaultFile()
    }
  }
}
</script>

<style lang="scss">
.off-add {
  .el-upload--picture-card {
    display: none;
  }
}
</style>
