/**
 * (ajax)
 */

import axios from 'axios'

import local from './local'
import { Message } from 'element-ui'
import router from '../router'
// import store from '../store'
import { get, toNumber } from 'lodash'
import NProgress from 'nprogress'

/* qs ;  ===>api  ===>pron; Content-Type = "application/json" */
axios.defaults.baseURL =
  process.env.NODE_ENV === 'development' ? '/api' : '/pron'
axios.defaults.headers.common['Content-Type'] = 'application/json'

/* to do ID */
// 
// axios.defaults.headers.common.tenantId = '1'
// axios.defaults.headers.common.orgId = '0'
//

axios.defaults.timeout = 20000 // 

const pending = [] // ajaxajax
const cancelToken = axios.CancelToken
const removePending = config => {
  for (const p in pending) {
    if (
      pending[p].u ===
      config.url +
        '&' +
        config.method +
        '&' +
        JSON.stringify(config.data) +
        '&' +
        JSON.stringify(config.params)
    ) {
      // 
      pending[p].f() // 
      pending.splice(p, 1) // 
    }
  }
}

// 
const errorHandler = error => {
  NProgress.done()
  const status = toNumber(get(error, 'response.status'))
  const msg = get(error, 'response.data.msg')
  switch (status) {
  /* eslint-disable no-param-reassign */
    case 400:
      error.message = msg || ''
      break
    case 401:
      // TODO 
      error.message = msg || ''
      local.clear() // 401401token
      router.push('/login')
      break
    case 403:
      error.message = msg || ''
      break
    case 404:
      error.message = msg || `: ${error.response.config.url}`
      break
    case 408:
      error.message = msg || ''
      break
    case 500:
      error.message = msg || ''
      break
    case 501:
      error.message = msg || ''
      break
    case 502:
      error.message = msg || ''
      break
    case 503:
      error.message = msg || ''
      break
    case 504:
      error.message = msg || ''
      break
    case 505:
      error.message = msg || 'HTTP'
      break
    default:
      break
    /* eslint-disabled */
  }
  if (error) {
    Message.error(error.message)
  }
  return Promise.reject(error)
}

// 
axios.interceptors.request.use(config => {
  NProgress.start()
  // token
  const afterLogin = local.get('afterLogin')
  // const curOrg = store.state.curOrg

  config.headers.Authorization =
    'Basic ' + window.btoa('system-service' + ':' + 'system-service')
  if (afterLogin !== null) {
    config.headers.Authorization = 'bearer ' + afterLogin
  }
  // if (curOrg) {
  //   config.headers.orgId = curOrg.orgId
  // } else {
  //   // orgId1
  //   config.headers.orgId = process.env.NODE_ENV === 'development' ? 1 : 0
  // }
  removePending(config) // ajax
  /* eslint-disable */
  config.cancelToken = new cancelToken(c => {
    // ajax&
    pending.push({
      u:
        config.url +
        '&' +
        config.method +
        '&' +
        JSON.stringify(config.data) +
        '&' +
        JSON.stringify(config.params),
      f: c
    })
  })
  return config
})

// 
axios.interceptors.response.use(response => {
  NProgress.done()
  removePending(response.config) // ajaxpending
  const {
    data: { code, msg, success }
  } = response
  //   
  if (success === false) {
    Message.error(msg)
    if (router.currentRoute.path === '/login') {
      local.clear() // successfalsetokentokenafterLogin
    } else {
      if (code === '00402') {
        local.clear() // 00402tokentoken
        router.push('/login')
      }
    }
  } else {
    return response
  }
}, errorHandler)

// getpost DELETE PUT
export default {
  get(url, data = {}) {
    return new Promise((resolve, reject) => {
      axios
        .get(url, { params: data })
        .then(response => {
          if (response && response.data) {
            resolve(response.data)
          } else {
            reject(response)
          }
        })
        .catch(err => {
          reject(err)
        })
    })
  },
  post(url, data = {}) {
    return new Promise((resolve, reject) => {
      axios
        .post(url, data)
        .then(response => {
          if (response && response.data) {
            resolve(response.data)
          } else {
            reject(response)
          }
        })
        .catch(err => {
          reject(err)
        })
    })
  },
  put(url, data = {}) {
    return new Promise((resolve, reject) => {
      axios
        .put(url, data)
        .then(response => {
          if (response && response.data) {
            resolve(response.data)
          } else {
            reject(response)
          }
        })
        .catch(err => {
          reject(err)
        })
    })
  },
  delete(url, data = {}) {
    return new Promise((resolve, reject) => {
      axios
        .delete(url, { params: data })
        .then(response => {
          if (response && response.data) {
            resolve(response.data)
          } else {
            reject(response)
          }
        })
        .catch(err => {
          reject(err)
        })
    })
  }
}
