const system = '/system' // -serive
// warehouse
const warehouse = '/warehouse'
// warehouse-service
const sales = '/sales'
const goodsService = '/goods' // goods-service
const finance = '/financial'
export { procurement, system, sales, warehouse, goodsService, finance }
