<template>
  <div class="table-pro">
    <y-form
      v-bind="$attrs"
      v-on="$listeners"
      v-model="queryParams"
      :config="config"
      :inline="true"
      label-position="left"
      :label-width="$attrs['label-width']"
    >
      <slot name="form">
        <el-button @click="handleQuery"></el-button>
      </slot>
    </y-form>
    <y-table
      :data="tableData"
      :columns="columns"
      pagination
      :total="total"
      :reload="reloadData"
      v-bind="$attrs"
      v-on="$listeners"
    >
      <slot name="table"></slot>
    </y-table>
  </div>
</template>

<script>
import request from '../../../examples/utils/request'
import { filter, merge, toNumber } from 'lodash'
import { defaultColumn } from './config'
export default {
  name: 'YTablePro',
  data() {
    return {
      tableData: [], // 
      columns: [], // 
      queryParams: {
        pageNo: 1,
        pageSize: 10
      },
      config: {}, // 
      total: 0

    }
  },
  props: {
    url: String // url
    // columns 
  },
  components: {
  },
  watch: {
    url(val) {
      this.handleQuery()
    }
  },
  mounted() {
    this.initConfig()
    this.loadData()
  },
  methods: {
    initConfig() {
      // 
      this.columns = filter(this.$attrs.columns, column => !column.hidden)
      const filterColumns = filter(this.$attrs.columns, column => column.filter)
      filterColumns.forEach(column => {
        const key = column.prop
        // 
        this.$set(this.config, key, { ...defaultColumn, ...column })

        // 
        this.queryParams[key] = ''
      })

      console.log('config', this.config)
    },
    /**
    * 
    */
    async loadData() {
      // 
      const res = await request.get(this.url, this.queryParams)
      if (res.success && res.code === '200') {
        this.tableData = res.data.records
        this.total = toNumber(res.data.total)
      }
    },
    /**
    * 
    */
    handleQuery() {
      // pageNo1
      this.queryParams = merge(this.queryParams, { pageNo: 1 })
      this.loadData()
    },
    /**
     * 
     */
    reloadData({ pageSize, currentPage, type }) {
      if (type === 'size-change') {
        // pageNo1
        this.queryParams = merge(this.queryParams, { pageSize, pageNo: 1 })
      } else {
        // 
        this.queryParams = merge(this.queryParams, { pageNo: currentPage })
      }
      this.loadData()
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
